<template>
  <div id="Form" v-bind:class="{element: anim}">
    <h1>Шаг 2</h1>
    <div class="textInputs">
      <form v-bind:class="{inv: error}">
        <strong>Почтовый индекс</strong><p v-bind:class="{error: step1.post === ''}">введите индекс</p>
        <input class="textInput" type="text" v-model="step1.post">
      </form>
      <form v-bind:class="{inv: error}">
        <strong>Страна</strong><p v-bind:class="{error: step1.country === ''}">введите страну</p>
        <input class="textInput" type="text" v-model="step1.country">
      </form>
      <form v-bind:class="{inv: error}">
        <strong>Область</strong><p v-bind:class="{error: step1.area === ''}">введите область</p>
        <input class="textInput" type="text" v-model="step1.area">
      </form>
      <form v-bind:class="{inv: error}">
        <strong>Город</strong><p v-bind:class="{error: step1.city === ''}">введите город</p>
        <input class="textInput" type="text" v-model="step1.city">
      </form>
      <form v-bind:class="{inv: error}">
        <strong>Улица</strong><p v-bind:class="{error: step1.street === ''}">введите улицу</p>
        <input class="textInput" type="text" v-model="step1.street">
      </form>
      <form v-bind:class="{inv: error}">
        <strong>Дом</strong><p v-bind:class="{error: step1.house === ''}">введите номер дома</p>
        <input class="textInput" type="text" v-model="step1.house">
      </form>
    </div>
    <div v-on:click="greet">
      <Button text="ШАГ 3 >"/>
    </div>
  </div>
</template>

<script>
import Button from '../components/Button.vue'

export default {
  name: 'FormBlue',
  components: {
    Button
  },
  data(){
    return{
      anim: false,
      error: false,
      step1:{post:'',country:'',area:'',city: '',street:'',house:''}  ,   
    }
  },
    methods: {
    greet: function () {
      if (this.step1.post !== '' && this.step1.country !== '' && this.step1.city !== '' && this.step1.street !== '' && this.step1.house !== '' && this.step1.area !== '') {
        this.anim = !this.anim
        this.$emit('add-client', this.step1)
      } else {
        this.error = true
      }
    }
  }
}
</script>

<style scoped>
form{
  position: relative;
}
p{
  color: rgb(255, 147, 147);
  font-size: 17.5px;
  position: absolute;
  right: 0;
  top: 0px;
  margin-top: 3px;
  display: none;
  z-index: -100;
}

.error{
  display: block;
}

.inv p{
  z-index: 1000;
}
h1 {
  margin: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  margin-left: 20px;
}
#Form {
  position: absolute;
  width: 50%;
  height: 450px;
  top: 130px;
  margin-left: 30px;
  left: 35%;
  background-color: #5990d8 ;
  border-radius: 20px;
  -webkit-box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
  box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
}
.textInputs{
  width: 90%;
  margin-left: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}
strong{
  width: 100%;
  font-size: 20px;
}
.textInput{
  width: 100%;
  font-size: 18px;
  margin-top: 5px;
  border: none;
  border-radius: 5px;
  padding: 5px 0 5px 5px;
}
input:focus{
  outline: none;
  -webkit-box-shadow: inset 0px -61px 2px -56px rgb(143, 255, 143);
  -moz-box-shadow: inset 0px -61px 2px -56px rgba(143, 255, 143);
  box-shadow: inset 0px -61px 2px -56px rgba(143, 255, 143);

}
label{
  margin-left: 5%;
  width: 100%;
  font-size: 20px;
}
label input{
  margin-right: 10px;
  margin-top: 50px;
}
Button{
  color: #5990d8;
}
.element{
  z-index: -1;
  animation: animationFrames linear 4s;
  animation-iteration-count: 1;
  transform-origin: 50% 50%;
  animation-fill-mode:forwards;
  -webkit-animation: animationFrames linear 4s;
  -webkit-animation-iteration-count: 1;
  -webkit-transform-origin: 50% 50%;
   -webkit-animation-fill-mode:forwards; 
  -moz-animation: animationFrames linear 4s;
  -moz-animation-iteration-count: 1;
  -moz-transform-origin: 50% 50%;
  -moz-animation-fill-mode:forwards;
  -o-animation: animationFrames linear 4s;
  -o-animation-iteration-count: 1;
  -o-transform-origin: 50% 50%;
  -o-animation-fill-mode:forwards;
  -ms-animation: animationFrames linear 4s;
  -ms-animation-iteration-count: 1;
  -ms-transform-origin: 50% 50%;
  -ms-animation-fill-mode:forwards;
}

@keyframes animationFrames{
  0% {
    transform:  translate(0px,0px)  ;
  }
  7% {
    transform:  translate(-1700px,-43px)  ;
  }
  13% {
    transform:  translate(30px,-40px)  ;
  }
  100% {
    transform:  translate(30px,-40px)  ;
  }
}

@-moz-keyframes animationFrames{
  0% {
    -moz-transform:  translate(0px,0px)  ;
  }
  7% {
    -moz-transform:  translate(-1700px,-43px)  ;
  }
  13% {
    -moz-transform:  translate(30px,-40px)  ;
  }
  100% {
    -moz-transform:  translate(30px,-40px)  ;
  }
}

@-webkit-keyframes animationFrames {
  0% {
    -webkit-transform:  translate(0px,0px)  ;
  }
  7% {
    -webkit-transform:  translate(-1700px,-43px)  ;
  }
  13% {
    -webkit-transform:  translate(30px,-40px)  ;
  }
  100% {
    -webkit-transform:  translate(30px,-40px)  ;
  }
}

@-o-keyframes animationFrames {
  0% {
    -o-transform:  translate(0px,0px)  ;
  }
  7% {
    -o-transform:  translate(-1700px,-43px)  ;
  }
  13% {
    -o-transform:  translate(30px,-40px)  ;
  }
  100% {
    -o-transform:  translate(30px,-40px)  ;
  }
}

@-ms-keyframes animationFrames {
  0% {
    -ms-transform:  translate(0px,0px)  ;
  }
  7% {
    -ms-transform:  translate(-1700px,-43px)  ;
  }
  13% {
    -ms-transform:  translate(30px,-40px)  ;
  }
  100% {
    -ms-transform:  translate(30px,-40px)  ;
  }
}


@media screen and (max-width: 1300px) {
  #Form{
    width: 86%;
    left: 7%;
    top:180px;
    margin-left: 0px;
  }
    .element{
    z-index: 1;
    animation: none;
    display: none;
    transition: all 200px;
  }
}
</style>
