<template>
  <div id="Form" v-bind:class="{element: anim}">
    <h1>Шаг 3</h1>
        <div class="textInputs">
      <form v-bind:class="{inv: error}">
        <strong>Тип документа</strong><p v-bind:class="{error: step1.type === ''}">выберите тип документа</p>
        <select class="textInput select"  v-model="step1.type">
          <option>Паспорт</option>
          <option>Свидетельство о рождении</option>
          <option>Вод. удостоверение</option>
        </select>
      </form>
            <form v-bind:class="{inv: error}">
        <strong>Серия</strong><p v-bind:class="{error: step1.id === ''}">введите серию</p>
        <input class="textInput" type="text" v-model="step1.id">
      </form>
            <form v-bind:class="{inv: error}">
        <strong>Номер</strong><p v-bind:class="{error: step1.num === ''}">введите номер</p>
        <input class="textInput" type="text" v-model="step1.num">
      </form>
            <form v-bind:class="{inv: error}">
        <strong>Кем выдан</strong><p v-bind:class="{error: step1.who === ''}">введите кем выдан</p>
        <input class="textInput" type="text" v-model="step1.who">
      </form>
            <form v-bind:class="{inv: error}">
        <strong>Дата выдачи</strong><p v-bind:class="{error: step1.date === ''}">введите дату</p>
        <input class="textInput" type="text" v-model="step1.date">
      </form>
    </div>
      <div v-on:click="greet">
        <Button text="ЗАКОНЧИТЬ" v-bind:done="done"/>
      </div>
  </div>
</template>

<script>
import Button from '../components/Button.vue'

export default {
  name: 'FormRed',
  components: {
    Button
  },
  data(){
    return{
      end:true,
      error: false,
      step1:{type:'',id:'',num:'',who: '',date:''}
    }
  },
    methods: {
    greet() {
      if (this.step1.type !== '' && this.step1.id !== '' && this.step1.num !== '' && this.step1.who !== '' && this.step1.date !== '') {
        this.$emit('add-client', this.step1)
        this.$emit('end', this.end)
      } else {
        this.error = true
      }
    }
  }
}
</script>

<style scoped>
form{
  position: relative;
}
p{
  color: rgb(255, 147, 147);
  font-size: 17.5px;
  position: absolute;
  right: 0;
  top: 0px;
  margin-top: 3px;
  display: none;
  z-index: -100;
}

.error{
  display: block;
}

.inv p{
  z-index: 1000;
}
h1 {
  margin: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  margin-left: 20px;
}
#Form {
  position: absolute;
  width: 50%;
  height: 450px;
  top: 90px;
  margin-left: 60px;
  left: 35%;
  background-color: #d85959 ;
  border-radius: 20px;
  -webkit-box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
  box-shadow: 5px 5px 23px -5px rgba(0,0,0,0.75);
}
.textInputs{
  width: 90%;
  margin-left: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}
strong{
  width: 100%;
  font-size: 20px;
}
.textInput{
  width: 100%;
  font-size: 18px;
  margin-top: 5px;
  border: none;
  border-radius: 5px;
  padding: 5px 0 5px 5px;
}
input:focus{
  outline: none;
  -webkit-box-shadow: inset 0px -61px 2px -56px rgb(143, 255, 143);
  -moz-box-shadow: inset 0px -61px 2px -56px rgba(143, 255, 143);
  box-shadow: inset 0px -61px 2px -56px rgba(143, 255, 143);

}
label{
  margin-left: 5%;
  width: 100%;
  font-size: 20px;
}
label input{
  margin-right: 10px;
  margin-top: 50px;
}
Button{
  width: 60%;
  left: 20%;
  bottom: 40px;
  max-height: 50px;
  color: #d85959;
}


@media screen and (max-width: 1300px) {
  #Form{
    width: 86%;
    left: 7%;
    top:140px;
    margin-left: 0px;
  }
}
</style>
