<template>
  <div id="app">
    <info v-bind:clients="clients"/>
    <div class="done" v-bind:class="{drop: end}">Новый клиент добавлен!
      <button v-on:click="done">OK</button>
    </div>
    <div class="forms" v-bind:class="{none: end}">
      <FormRed @add-client="addClient" @end="pushClient"/>
      <FormBlue @add-client="addClient"/>
      <FormGreen @add-client="addClient"/>
    </div>
  </div>
</template>

<script>
import FormGreen from './components/FormGreen.vue'
import FormBlue from './components/FormBlue.vue'
import FormRed from './components/FormRed.vue'
import Info from './components/Info.vue'
import Button from './components/Button.vue'

export default {
  name: 'App',
  data() {
    return{
      end:false,
      inform: [],
      clients: [
        {name:'New',surname:'Client', id:1}
      ]
    }
  },
  methods:{
    done(){
      this.clients = this.inform
    },
    pushClient(t){
      this.end = t
    },  
    addClient(step1){
      this.inform.push(step1)
    },
  },
  components: {
    FormGreen,
    FormBlue,
    FormRed,
    Button,
    Info
  }
}
</script>

